@import "../../../styles/all";

.commentWrapper {
  width: calc($postWidth - 50px);
  min-height: 40px;
  margin-bottom: 8px;
  background-color: white;
  padding: 10px;
  display: flex;

  .commentInfo {
    display: flex;
    flex-direction: column;
    .commentUserInfo {
      @extend %flexCenter;
      margin-bottom: 4px;
      img {
        width: 20px;
        height: 20px;
        border-radius: 50%;
      }
    }

    .userName {
      font-size: 0.9rem;
      margin-left: 5px;
    }
  }

  .commentCreatedAt {
    font-size: 0.7rem;
    // padding-left: 5px;
  }

  .commentText {
    line-height: 1.05rem;
    font-size: 1.03rem;
    margin-left: 15px;
    align-self: center;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}

@media (max-width: 870px) {
  .commentWrapper {
    width: calc($postWidthMobile - 50px);
  }
}
